"use client";

import Link from "next/link";
import { useSelectedLayoutSegment } from "next/navigation";
import { Layers, LogOut, User as UserIcon, Code } from "lucide-react";
import { ModeToggle } from "@/components/ui/mode-toggle";
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { useSession, signOut } from "@/lib/auth-client";

export default function Header() {
  const { data: session, status } = useSession();
  const segment = useSelectedLayoutSegment();

  const handleSignOut = async () => {
    await signOut();
    // Redirection ou logique supplémentaire après la déconnexion si nécessaire
  };

  return (
    <header className="sticky top-0 z-50 w-full border-b border-[var(--border)] bg-[var(--background)/95] backdrop-blur supports-[backdrop-filter]:bg-[var(--background)/60]">
      <div className="container flex h-16 max-w-screen-2xl items-center">
        <Link href="/" className="mr-6 flex items-center space-x-2">
          <Code className="h-6 w-6 text-[var(--primary)]" />
          <span className="font-bold sm:inline-block text-[var(--foreground)]">
            ShowStack
          </span>
        </Link>
        <nav className="flex items-center gap-6 text-sm">
          <Link
            href="/dashboard"
            className={`transition-colors hover:text-[var(--foreground)/80] ${
              segment === "dashboard"
                ? "text-[var(--foreground)]"
                : "text-[var(--foreground)/60]"
            }`}
          >
            Dashboard
          </Link>
          {/* Ajoutez d'autres liens de navigation ici si nécessaire */}
        </nav>
        <div className="flex flex-1 items-center justify-end space-x-4">
          <ModeToggle />
          {status === "authenticated" && session?.user ? (
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button
                  variant="ghost"
                  className="relative h-8 w-8 rounded-full"
                >
                  <Avatar className="h-8 w-8">
                    <AvatarImage
                      src={session.user.image || undefined}
                      alt={session.user.name || "User"}
                    />
                    <AvatarFallback>
                      {session.user.name
                        ? session.user.name.charAt(0).toUpperCase()
                        : "U"}
                    </AvatarFallback>
                  </Avatar>
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent className="w-56" align="end" forceMount>
                <DropdownMenuLabel className="font-normal">
                  <div className="flex flex-col space-y-1">
                    <p className="text-sm font-medium leading-none text-[var(--foreground)]">
                      {session.user.name}
                    </p>
                    <p className="text-xs leading-none text-[var(--muted-foreground)]">
                      {session.user.email}
                    </p>
                  </div>
                </DropdownMenuLabel>
                <DropdownMenuSeparator />
                <DropdownMenuItem asChild>
                  <Link href="/dashboard">
                    {" "}
                    {/* Adaptez le lien si vous avez une page de profil dédiée */}
                    <UserIcon className="mr-2 h-4 w-4" />
                    <span>Profil</span>
                  </Link>
                </DropdownMenuItem>
                <DropdownMenuSeparator />
                <DropdownMenuItem onClick={handleSignOut}>
                  <LogOut className="mr-2 h-4 w-4" />
                  <span>Déconnexion</span>
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          ) : (
            <Button asChild>
              <Link href="/auth/signin">Se connecter</Link>
            </Button>
          )}
        </div>
      </div>
    </header>
  );
}
